.nav-breadcrumb-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #606162;
  width: 55px;
  height: 16px;
  margin-top: 0px;
  margin-bottom: 0;
  position: relative;
  -ms-flex-item-align: start;
      align-self: flex-start;
  gap: 8px;
  -webkit-transition: margin-left gap 0.1s ease;
  -o-transition: margin-left gap 0.1s ease;
  transition: margin-left gap 0.1s ease;
}

.nav-breadcrumb-container:hover {
  gap: 10px;
}

.nav-breadcrumb-container:hover .nav-breadcrumb-title {
  color: #0076d5;
}

.nav-breadcrumb-container:hover path {
  fill: #0076d5;
}

.nav-breadcrumb-title {
  display: -ms-flexbox;
  display: flex;
  justify-self: flex-end;
  width: 37px;
  font-style: normal;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  /* color: #606162; */
  /* margin-left: 8px; */
  padding: 0;
}

.nav-breadcrumb-icon {
  display: -ms-flexbox;
  display: flex;
  margin: 4px 0 0 2px;
  padding: 0;
  min-width: 10px;
  height: 8px;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .nav-breadcrumb-container {
    margin: 9px 0 9px 14px;
  }

  .nav-breadcrumb-container:hover {
    margin: 9px 0 9px 12px;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .nav-breadcrumb-container {
    margin: 13px 0 13px 62px;
  }

  .nav-breadcrumb-container:hover {
    margin: 13px 0 13px 60px;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .nav-breadcrumb-container {
    margin: 13px 0 13px 62px;
  }

  .nav-breadcrumb-container:hover {
    margin: 13px 0 13px 60px;
  }
}



