  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  -webkit-transition: color background-color border-color 0.1s;
  -o-transition: color background-color border-color 0.1s;
  transition: color background-color border-color 0.1s;
}

.bundle-btn:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
