.footer-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* font-family: "CiscoSansTT-Medium", sans-serif; */
  font-family: "CiscoSansTTLight", sans-serif;
  color: #606162;
}

.main {
  font-size: 14px;
  line-height: 20px;
}

.sub {
  font-size: 12px;
  line-height: 16px;
}

.soMe-icon {
  padding: 33px 0 45px 0;
}

.copyright {
  font-size: 12px;
  line-height: 16px;
}

.accordion {
  font-size: 14px;
  line-height: 20px;
  padding: 19px 0;
  border-bottom: 1px solid #e6e7e8;
}

.accordion-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  cursor: pointer;
}

.content-item {
  margin-top: 24px;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .footer-container {
    margin: 13px 5vw 32px 5vw;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .footer-container {
    margin: 13px 10.42vw 32px 10.42vw;
  }
}

@media all and (min-width: 1024px) {
  /* For desktop */
  .footer-container {
    margin: 43px 7.81vw 28px 7.81vw;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .main {
    padding-bottom: 27px;
    border-bottom: 1px solid #e6e7e8;
    width: 100%;
    text-align: center;
  }

  .sub {
    padding: 21px 0;
    border-bottom: 1px solid #e6e7e8;
    width: 100%;
    text-align: center;
  }

  .soMe-icon {
    padding: 24px 0 29px 0;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .footer-container {
    margin: 48px 8.89vw 32px 8.89vw;
    -ms-flex-align: initial;
        align-items: initial;
  }

  .upper-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-bottom: 1px solid #e6e7e8;
    padding-bottom: 47px;
  }

  .main {
    padding-bottom: 0;
    border-bottom: none;
    width: auto;
  }

  .sub {
    padding: 0;
    border-bottom: none;
    width: auto;
  }

  .soMe-icon {
    padding: 0;
  }

  .lower-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding-top: 21px;
  }
}



