.demo-container {
  display: -ms-flexbox;
  display: flex;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.democard-title {
  font-size: 32px;
  line-height: 40px;
  white-space: pre;
  margin-bottom: 16px;
}

.democard-content {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 21px;
}

.democard-launch {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: relative;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
}

.democard-launch::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 123px;
  height: 1px;
  background-color: #0076d5;
  -webkit-transform: scale(0, 1);
      -ms-transform: scale(0, 1);
          transform: scale(0, 1);
}

.democard-launch:hover::after {
  -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
          transform: scale(1, 1);
}

.democard-button {
  display: -ms-flexbox;
  display: flex;
  padding-right: 11.5px;
  font-style: normal;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #0076d5;
  margin-right: 0px;
  padding-top: 0.5px;
  padding-bottom: 2.5px;
  -webkit-transition: margin-right padding-top 0.1s;
  -o-transition: margin-right padding-top 0.1s;
  transition: margin-right padding-top 0.1s;
  font-family: "CiscoSansTT-Medium", sans-serif;
}

.democard-button:hover {
  margin-right: 2px;
  padding-top: 0px;
}

.democard-icon-greater {
  display: -ms-flexbox;
  display: flex;
  margin-top: 4.5px;
  margin-left: 0px;
  padding-top: 3px;
  width: 5.5px;
  height: 10.98px;
  -webkit-transition: margin-left 0.1s;
  -o-transition: margin-left 0.1s;
  transition: margin-left 0.1s;
  fill: #0076d5;
}

.democard-icon-greater:hover {
  margin-left: 2px;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
