    margin: 80px 8.89vw;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .demo-image {
    max-width: 387px;
    max-height: 218px;
    -ms-flex-item-align: center;
        align-self: center;
  }

  .democard {
    width: 33.76vw;
    padding-top: 0;
    padding-bottom: 0;
