    padding-right: calc((8.44vw) + 24px);
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .demo-container {
    -ms-flex-direction: row;
        flex-direction: row;
    margin: 80px 8.89vw;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .demo-image {
    max-width: 531px;
    max-height: 300px;
  }

  .democard {
    width: 32.89vw;
    padding-top: 31.5px;
    padding-bottom: 31.5px;
    padding-left: 24px;
    padding-right: calc((8.22vw) + 24px);
  }
}



