.webex-conainer {
  margin: 0;
  padding: 0;
  background-color: #000038;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #ffffff;
}

.webex-button {
  background-color: #0076d5;
  width: 159px;
  height: 56px;
  border: 1px solid #0076d5;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #ffffff;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  -webkit-transition: color background-color border-color 0.1s;
  -o-transition: color background-color border-color 0.1s;
  transition: color background-color border-color 0.1s;
  /* align-self: center; */
}

.webex-content:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .webex-conainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .webex-header {
    width: auto;
    margin: 48px 5% 0 5%;
    font-size: 32px;
    line-height: 40px;
  }

  .webex-body {
    width: auto;
    margin: 48px 5% 48px 5%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .webex-content {
    margin: 0 0 48px 0;
    font-size: 20px;
    line-height: 26px;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .webex-conainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .webex-header {
    width: auto;
    margin: 64px 10.42% 0 10.42%;
    font-size: 32px;
    line-height: 40px;
  }

  .webex-body {
    width: auto;
    margin: 48px 10.42% 64px 10.42%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .webex-content {
    margin: 0 0 48px 0;
    font-size: 20px;
    line-height: 26px;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .webex-conainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .webex-header {
    width: calc((32.89%) - 14.4px);
    margin: 80px 24px 80px 8.89%;
    font-size: 48px;
    line-height: 56px;
  }

  .webex-body {
    width: calc((49.33%) - 9.6px);
    margin: 80px 8.89% 80px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .webex-content {
    margin: 0 0 48px 0;
    font-size: 20px;
    line-height: 26px;
  }
}



