.navglobal-container {
  display: -ms-flexbox;
  display: flex;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.nav-accordion {
  width: 100vw;
}

.nav-accordion-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.nav-accordion-title-right-icons {
  display: -ms-flexbox;
  display: flex;
  gap: 16px;
}

.nav-accordion-menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.menu-item > p {
  margin: 0;
  padding: 0;
  max-height: 24px;
}

.menu-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  max-height: 24px;
  font-size: 16px;
  line-height: 24px;
  border-bottom: 1px solid #e6e7e8;
}

.menu-link {
  color: #0076d5;
  font-size: 16px;
  line-height: 24px;
}

.menu-item-lower {
  font-size: 14px;
  line-height: 20px;
}

.menu-lower-image {
  margin-right: 7px;
}

.menu-button {
  background-color: #0076d5;
  width: 89px;
  height: 40px;
  border: 1px solid #0076d5;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #ffffff;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  -webkit-transition: color background-color border-color 0.1s;
  -o-transition: color background-color border-color 0.1s;
  transition: color background-color border-color 0.1s;
  /* align-self: center; */
}

.menu-button:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .nav-accordion-title {
    width: 90vw;
    margin: 16px 5vw;
  }

  .nav-accordion-logo {
    max-width: 59px;
    max-height: 31px;
  }

  .nav-accordion-clese {
    padding-top: 18px;
    padding-bottom: 18px;
    padding-left: calc((100vw) - 44px);
    padding-right: 24px;
    border-bottom: 1px solid #e6e7e8;
  }

  .menu-item {
    margin: 0 16px;
    padding: 11px 0;
  }

  .menu-link {
    margin: 26.5px 0 20px 16px;
  }

  .menu-item-lower {
    margin: 0 0 14px 16px;
  }

  .menu-button-container {
    margin: 14.5px 0 48px 16px;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .nav-accordion-title {
    width: 83.34vw;
    margin: 16px 8.33vw;
  }

  .nav-accordion-logo {
    max-width: 74px;
    max-height: 39px;
  }

  .nav-accordion-clese {
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: calc((100vw) - 44px);
    padding-right: 24px;
    border-bottom: 1px solid #e6e7e8;
  }

  .menu-item {
    margin: 0 24px;
    padding: 12px 0;
  }

  .menu-link {
    margin: 26.5px 0 21px 24px;
  }

  .menu-item-lower {
    margin: 0 0 14px 24px;
  }

  .menu-button-container {
    margin: 14px 0 48px 24px;
  }
}

@media all and (min-width: 1024px) {
  /* For desktop */
  .navglobal-container {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .navglobal-upper {
    background-color: #f4f5f7;
    padding: 7px 64px 7px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    gap: 31px;
  }

  .upper-image {
    padding-left: 8px;
  }

  .navglobal-lower {
    padding: 13.5px 64px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .lower-left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    gap: 32px;
  }

  .lower-item {
    font-size: 14px;
    line-height: 20px;
  }

  .lower-right {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    gap: 32px;
  }

  .lower-right-text {
    font-size: 14px;
    line-height: 20px;
  }
}



