.marquee-conainer {
  /* font-family: "CiscoSansTT-Medium", sans-serif; */
  font-family: "CiscoSansTTLight", sans-serif;
}

.marquee-card-button {
  background-color: #0076d5;
  width: 203px;
  height: 56px;
  border: 1px solid #0076d5;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #ffffff;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  -webkit-transition: color background-color border-color 0.1s;
  -o-transition: color background-color border-color 0.1s;
  transition: color background-color border-color 0.1s;
  /* align-self: center; */
}

.marquee-card-button:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

.marquee-card-subtitle {
  font-size: 16px;
  line-height: 24px;
}

.marquee-card-title {
  font-family: "CiscoSansTTLight", sans-serif;
}

.marquee-card-content {
  font-family: "CiscoSansTTLight", sans-serif;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 41px;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .marquee-container {
    background-color: #ffffff;
    color: #1b1c1d;
    padding: 0;
  }

  .marquee-image {
    display: -ms-flexbox;
    display: flex;
    width: 100vw;
    max-height: 190px;
  }

  .marquee-card {
    width: 90vw;
    margin: 27px 5vw 42px 5vw;
  }

  .marquee-card-title {
    font-size: 42px;
    line-height: 50px;
    margin: 16px 0 15px 0;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .marquee-container {
    background-color: #ffffff;
    color: #1b1c1d;
    padding: 0;
  }

  .marquee-image {
    display: -ms-flexbox;
    display: flex;
    width: 100vw;
    max-height: 325px;
    background-color: #000038;
  }

  .marquee-card {
    width: 79.16vw;
    margin: 28px 10.42vw 64px 10.42vw;
  }

  .marquee-card-title {
    font-size: 56px;
    line-height: 64px;
    margin: 15px 0 13px 0;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .marquee-container {
    background-color: #000038;
    color: #ffffff;
    padding: 73px 8.89vw 117px 8.89vw;
  }

  .marquee-image {
    display: none;
  }

  .marquee-card {
    width: calc((41.11vw) - 12px);
    margin: 46px 0 0 0;
  }

  .marquee-card-title {
    font-size: 72px;
    line-height: 84px;
    margin: 12px 0 8px 0;
  }
}



