.nav-secondary-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-size: 14px;
  line-height: 20px;
  gap: 64px;

  position: sticky;
  top: 0;
  background-color: #ffffff;
  z-index: 9999;
}

.nav-desktop-overview {
  font-family: "CiscoSansTT-Medium", sans-serif;
  color: #1b1c1d;
}

.nav-desktop-item {
  font-family: "CiscoSansTTLight", sans-serif;
  color: #606162;
}

.nav-secondary-accordion-upper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  gap: 8px;
}

@media (max-width: 319px) {
  body {
    display: none;
  }
}

/* For mobile phones: */
@media (min-width: 320px) {
  .nav-secondary-container {
    padding: 0;
  }

  .nav-secondary-accordion-upper {
    width: 90vw;
    padding: 14px 5vw 11px 5vw;
    border-bottom: 1px solid #e6e7e8;
  }

  .nav-secondary-accordion-menu {
    padding: 14px 5vw 11px 5vw;
  }

  .nav-desktop-overview {
    padding: 13px 0 10px 0;
  }

  .nav-desktop-item {
    padding: 13px 0 10px 0;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .nav-secondary-container {
    padding: 0;
  }

  .nav-secondary-accordion-upper {
    width: 79.18vw;
    padding: 19px 10.42vw;
    border-bottom: 1px solid #e6e7e8;
  }

  .nav-secondary-accordion-menu {
    padding: 19px 10.42vw;
  }

  .nav-desktop-overview {
    padding: 13px 0 10px 0;
  }

  .nav-desktop-item {
    padding: 13px 0 10px 0;
  }
}

@media all and (min-width: 1024px) {
  /* For desktop */
  .nav-secondary-container {
    padding: 18px 6.25vw 17px 6.25vw;
    border-bottom: 1px solid #e6e7e8;
  }

  .nav-desktop-item:hover {
    color: #0076d5;
  }

  .nav-desktop-overview {
    padding: 0;
  }

  .nav-desktop-item {
    padding: 0;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .nav-secondary-container {
    padding: 23px 8.89vw 22px 8.89vw;
    border-bottom: 1px solid #e6e7e8;
  }

  .nav-desktop-item:hover {
    color: #0076d5;
  }

  .nav-desktop-overview {
    padding: 0;
  }

  .nav-desktop-item {
    padding: 0;
  }
}



